@charset "UTF-8";
html {
  color: #000;
  background: #FFF; }

body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input, textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

fieldset, img {
  border: 0; }

address, caption, cite, code, dfn, em, strong, th, var {
  font-style: normal;
  font-weight: normal; }

ol, ul {
  list-style: none; }

caption, th {
  text-align: left; }

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal; }

q:before, q:after {
  content: ''; }

abbr, acronym {
  border: 0;
  font-variant: normal; }

sup {
  vertical-align: text-top; }

sub {
  vertical-align: text-bottom; }

input, textarea, select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  *font-size: 100%; }

legend {
  color: #000; }

html {
  font-family: Verdana, "メイリオ", Meiryo, "ＭＳ ゴシック", sans-serif; }

body {
  background: url("/images/launcher/bg_body.png") repeat 0 0 #ffffff;
  color: #341700;
  margin: 0;
  padding: 0; }

table,
th,
td {
  margin: 0;
  padding: 0;
  border: none;
  border-collapse: collapse;
  vertical-align: top; }

strong {
  font-weight: bold; }

em {
  font-style: oblique; }

del {
  text-decoration: line-through; }

#wrapper {
  margin: 5px 0 0 0; }

#banner {
  width: 0; }
  #banner div {
    margin: 0 0 5px 0; }

.article {
  margin: 0 0 30px 0; }

.head {
  background: url("/images/launcher/h2_bg.png") no-repeat 0 0 transparent;
  height: 61px;
  min-height: 77px;
  min-width: 420px;
  display: inline-block; }
  .head p {
    color: #4d7cac;
    margin: 9px 0 0 10px; }
  .head h2 {
    color: #ffffff;
    margin: 4px 0 0 10px;
    padding: 0;
    font-weight: bold; }

.box {
  padding: 0;
  margin: 0;
  border: 1px solid #4d7cac;
  background: #fbfeff; }
  .box h3 {
    background: url("/images/launcher/h3_bg.png") no-repeat 0 0 transparent;
    margin: 0;
    padding: 9px 0 6px;
    text-indent: 25px;
    min-height: 25px;
    display: block;
    font-weight: bold; }
  .box ul {
    border-top: none;
    background: #fbfeff;
    padding: 0;
    margin: 0;
    display: inline-block;
    min-width: 418px; }
  .box ul li {
    background: url("/images/launcher/list.png") no-repeat 0 1px transparent;
    margin: 8px 10px;
    padding: 0 0 0 14px;
    text-indent: 0;
    list-style-type: none; }

.gold_ball {
  color: #f3f300; }

.silver_ball {
  color: #CCCCCC; }

.bronze_ball {
  color: #663300; }

.red_ball {
  color: #CC0000; }

.blue_ball {
  color: #000099; }
